import "assets/home/about.scss";

function About() {
  const tags = [
    "HTML5",
    "CSS",
    "JavaScript",
    "React.js",
    "React Native",
    "Restful WebServices",
    "Git",
    "Firebase",
    "Java",
    "Svelte",
  ];

  return (
    <section id="about">
      <h1 className="title">
        01. <span>About Me </span>
      </h1>
      <br />
      <div className="content">
        <div>
          <div className="inner-text">
            <p>.   . </p>
            <p>
                      . 
            </p>
            <p>
                  {" "}
              <span></span>  .
            </p>
            <p>
              ,  , ,    
              . !
            </p>
            <p>
                <span>"</span>
              <strong>     .</strong>
              <span>"</span>
              
            </p>
            <p>       .</p>
            <p>     . </p>
          </div>
          <div className="skill-view">
            <h2>skills</h2>
            <div className="tag-wrap">
              {tags.map((tag, index) => {
                return (
                  <p className="tag" key={index}>
                    {tag}
                  </p>
                );
              })}
            </div>
          </div>
        </div>

        <div className="img-wrap">
          <img
            src={require("assets/image/who_am_I_logo.png")}
            alt="profile logo"
          ></img>
        </div>
      </div>
    </section>
  );
}

export default About;
