import { Link } from "react-scroll";
import "assets/home/home.css";

function Introduce({ step }) {
  return (
    <section id="home">
      {step > 6 ? <h1 className="greeting">Hello, I am </h1> : null}
      {step > 7 ? <h2 className="name">.</h2> : null}
      {step > 8 ? <h2 className="job">FE Developer </h2> : null}
      {step > 9 ? (
        <div>
          <p className="subInfo">
              CS    
          </p>

          <p className="subInfo">
                  
          </p>
          <p className="subInfo">
               FE    , fcm   
             
          </p>
          <p className="subInfo">
                   
          </p>
          <p className="subInfo">,  ,   </p>
        </div>
      ) : null}

      {step > 10 ? (
        <Link
          activeClass="active"
          to="contact"
          spy={true}
          smooth={true}
          offset={-100}
          duration={500}
        >
          <button className="contact-btn">Contact me</button>
        </Link>
      ) : null}
    </section>
  );
}

export default Introduce;
