import { MailForm } from "./MailForm";
import "assets/home/contact.css";
import { useState } from "react";

function Contact() {
  const [open, setOpen] = useState(false);

  const onClickFormButton = (e) => {
    e.preventDefault();
    setOpen(true);
  };

  const onClickGitButton = (e) => {
    e.preventDefault();
    window.open("https://github.com/LireEruel");
  };

  const onClickVelogButton = (e) => {
    e.preventDefault();
    window.open("https://velog.io/@lire_eruel");
  };

  return (
    <section id="contact">
      <h1 className="title">
        03. <span>Contact</span>
      </h1>
      <div className="contact-content">
        <div className="contact-info">
          <h1>Contact me</h1>
          <p>    .</p>
          <p>        .</p>
          <p>jsh001505@naver.com</p>
          <button onClick={onClickGitButton}>
            <img src={require("assets/image/git_icon.png")} />
          </button>
          <button onClick={onClickVelogButton}>
            <img src={require("assets/image/velog_icon.png")} />
          </button>
        </div>
        {open ? null : (
          <button onClick={onClickFormButton}>get in touch</button>
        )}

        {open ? <MailForm /> : null}
      </div>
    </section>
  );
}

export default Contact;
